import React, { PureComponent } from 'react';
import { Link } from 'react-router-dom'; 
import { connect } from 'react-redux';
import { Redirect } from 'react-router-dom';
import  { 
    LoginWrapper,
    LoginBox,
    Button
} from './style';
import { Logo } from '../../common/header/style';
import { actionCreator } from './store';

class Login extends PureComponent {
    render() {
        const { loginStatus } = this.props;
        if(!loginStatus){
            return (
                <LoginWrapper>
                    <Link to='/'>
                        <Logo />                
                    </Link>
                    <LoginBox>
                        <div className='header'>
                            <span className='sign_in'>登陆</span>
                            <span className='sign_up'>注册</span>
                        </div>
                        <div className='content'>
                            <i className='iconfont user'>&#xe604;</i>
                            <input type='text' placeholder='username' ref={(input) => {this.account = input}} />
                            <i className='iconfont pwd'>&#xe716;</i>
                            <input type='password' placeholder='password' ref={(input) => {this.pwd = input}} />
                        </div>
                        <div className='banner'>
                            <span className='remember'>
                                <input type='checkbox' />记住我
                            </span>
                            <span className='question'>登录遇到问题?</span>
                        </div> 
                        <Button 
                            onClick={() => this.props.login(this.account, this.pwd)}
                        >登录</Button> 
                    </LoginBox>
                </LoginWrapper>
            );
        } else{
            return <Redirect to='/' />
        }
    }
}

const mapState = (state) => ({
    loginStatus: state.getIn(['login', 'login'])
})

const mapDispatch = (dispatch) => ({
    login(username, pwd) {
        dispatch(actionCreator.login(username, pwd))
    }
})

export default connect(mapState, mapDispatch)(Login);